import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import * as Survey from "survey-angular";
import * as widgets from "surveyjs-widgets";
import * as SurveyPDF from "survey-pdf";
import "inputmask/dist/inputmask/phone-codes/phone.js";

widgets.jquerybarrating(Survey);
widgets.jqueryuidatepicker(Survey);

@Component({
  selector: "app-survey",
  template: `
    <div id="surveyElement"></div>
    <div id="pdf-preview"></div>
  `
})
export class SurveyComponent implements OnInit {
  @Output() submitSurvey = new EventEmitter<any>();
  @Input() disabled: boolean = false;
  @Input("json") json: any;
  @Input("data") data: any;
  private result: any;

  ngOnInit() {
    this.setTheme();
    try {
      const surveyModel = new Survey.Model(this.json);
      if (this.disabled) {
        surveyModel.mode = "display";
      }
      if (this.data) {
        surveyModel.data = this.data;
      }
      surveyModel.onComplete.add((result, options) => {
        this.submitSurvey.emit(result.data);
        this.result = result.data;
      });
      Survey.SurveyNG.render("surveyElement", { model: surveyModel });
    } catch (error) {}
  }

  saveSurveyPdf() {
    const options = {
      fontSize: 14,
      margins: {
        left: 10,
        right: 10,
        top: 18,
        bot: 10
      }
    };
    const surveyPDF = new SurveyPDF.SurveyPDF(this.json, options);
    surveyPDF.mode = "display";
    surveyPDF.data = this.result;
    const oldFrame = document.getElementById("pdf-preview-frame");
    if (oldFrame) {
      oldFrame.parentNode.removeChild(oldFrame);
    }

    surveyPDF.onRenderHeader.add(function(_, canvas) {
      const horizontalAlign: any = "left";
      canvas.drawImage({
        base64:
          "data:image/png;base64,",
        horizontalAlign,
        width: (canvas.rect.yBot - canvas.rect.yTop) * 0.6,
        height: (canvas.rect.yBot - canvas.rect.yTop) * 0.6,
        margins: {
          left: (canvas.rect.yBot - canvas.rect.yTop) * 0.2
        }
      });
    });
    surveyPDF.onRenderFooter.add(function(_, canvas) {
      const horizontalAlign: any = "right";
      canvas.drawText({
        text: "Generated via SurveyJS PDF library",
        fontSize: 10
      });
      canvas.drawText({
        text: canvas.pageNumber + "/" + canvas.countPages,
        fontSize: 10,
        horizontalAlign,
        margins: {
          right: 12
        }
      });
    });

    surveyPDF.raw("dataurlstring").then(function(dataurl) {
      var pdfEmbed = document.createElement("embed");
      pdfEmbed.setAttribute("id", "pdf-preview-frame");
      pdfEmbed.setAttribute("type", "application/pdf");
      pdfEmbed.setAttribute("style", "width:100%");
      pdfEmbed.setAttribute("height", "600");
      pdfEmbed.setAttribute("src", dataurl);
      var previewDiv = document.getElementById("pdf-preview");
      previewDiv.appendChild(pdfEmbed);
    });
  }
  setTheme() {
    const mainColor = "#00bf6f";
    const mainHoverColor = "#6fe06f";
    const textColor = "#4a4a4a";
    const headerColor = "#001629";
    const headerBackgroundColor = "#4a4a4a";
    const bodyContainerBackgroundColor = "#f8f8f8";

    const defaultThemeColorsSurvey =
      Survey.StylesManager.ThemeColors["default"];
    defaultThemeColorsSurvey["$main-color"] = mainColor;
    defaultThemeColorsSurvey["$main-hover-color"] = mainHoverColor;
    defaultThemeColorsSurvey["$text-color"] = textColor;
    defaultThemeColorsSurvey["$header-color"] = headerColor;
    defaultThemeColorsSurvey[
      "$header-background-color"
    ] = headerBackgroundColor;
    defaultThemeColorsSurvey[
      "$body-container-background-color"
    ] = bodyContainerBackgroundColor;

    Survey.StylesManager.applyTheme();
  }
}
